import React, { useEffect } from 'react';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { Box, NoSsr } from '@material-ui/core';
import { withSitecoreContext, Text } from '@sitecore-jss/sitecore-jss-react';
import {
  pushAnaylyticsData,
  createDynamicLabels,
} from '../../../../../src/utils/analytics';
import Grid from '../../../components/molecules/Grid';
import Title from '../../../components/atoms/Title';
import Typography from '../../../components/atoms/Typography';
import Button from '../../../components/atoms/Button';
import Picture from '../../../components/atoms/Picture';
import aspectRatios from '../../../utils/AspectRatios';
import { COMPONENT_NAME } from './locators';
import CustomVideo from '../../../components/atoms/Video';
import { plyrVideo } from '../../../utils/video';
import useStyles from './style.js';
import { withNamespaces } from 'react-i18next';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import YoukuPlayer from '../../../components/molecules/YoukuPlayer';
import { isInsideChina } from '../../../utils/cookie';
import { analyticsData } from '../../../../../src/constants/Analytics/analytics-data';
import {
  getComponentName,
  getPageName,
} from '../../../utils/getPageComponentInfo';
import RichText from '../../../components/atoms/RichText';

const Article = props => {
  const { fields, params, sitecoreContext } = props;
  const pageName = getPageName(props);
  const componentName = getComponentName(props);
  const classes = useStyles();
  let playerDOMRef = null;
  useEffect(() => {
    if (!isInsideChina(sitecoreContext) && videoUrl && videoUrl.length !== 0) {
      let plyrDOM = document.querySelector('.plyr-video');
      const plyrDOMInterval = setInterval(() => {
        plyrDOM = document.querySelector('.plyr-video');
        if (plyrDOM) {
          clearInterval(plyrDOMInterval);
          plyrVideo({
            playerDOMRef: playerDOMRef,
            analyticsData: analyticsData.pageBodyInteraction.videoPlayClick,
            titleText: anayltyicsTitle,
            shouldShowPoster: false,
          });
        }
      }, 100);
    }
  }, []);

  const setPlayerDOMRef = ref => {
    playerDOMRef = ref;
  };

  const { title, heading, description, media, cta1, cta2 } = fields;
  if (isEmpty(media)) {
    return null;
  }

  let { fields: mediaFields } = (media && media[0]) || {};
  let videoUrl = get(mediaFields, 'video.value.href', '') || '';

  // Desktop fallback image
  if (!get(mediaFields, 'image.value.src', '') && videoUrl.length === 0) {
    mediaFields.image = {
      value: {
        src: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAACCAQAAAA3fa6RAAAADklEQVR42mNkAANGCAUAACMAA2w/AMgAAAAASUVORK5CYII=',
        '3x2': {
          large: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAACCAQAAAA3fa6RAAAADklEQVR42mNkAANGCAUAACMAA2w/AMgAAAAASUVORK5CYII=',
          small: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAACCAQAAAA3fa6RAAAADklEQVR42mNkAANGCAUAACMAA2w/AMgAAAAASUVORK5CYII=',
        },
      },
    }
  }

  //mobile fallbackImage
  if (!get(mediaFields, 'mobileImage.value.src', '') && videoUrl.length === 0) {
    mediaFields.mobileImage = {
      ...mediaFields.image,
    };
  }

  //This will check user is inside china or chinese language is selected.
  if (isInsideChina(sitecoreContext)) {
    //check if variant is video and youku player url is there.
    videoUrl = get(mediaFields, 'youkuVideo.value.href', '') || '';
  }
  const anayltyicsTitle = createDynamicLabels([
    pageName,
    componentName,
    `${get(sitecoreContext, 'route.name', '')}-${heading && heading.value}`,
  ]);
  const addAnalyticsInfo = text => {
    const obj = {
      ...analyticsData.pageBodyInteraction.arrowClick,
      ...{
        label: createDynamicLabels([
          pageName,
          componentName,
          `${get(heading, 'value', '')} - ${text}`,
        ]),
      },
    };
    pushAnaylyticsData(obj);
  };

  return !isEmpty(fields) ? (
    <Grid className={classes.root} data-locator={COMPONENT_NAME}>
      <Grid
        container
        spacing={0}
        className={`rowWrapper ${params.variation !== 'Article_TextLeft' &&
          'rowReverse'}`}
        alignItems="center"
      >
        <Grid item xs={12} lg={6} className="descriptionArea">
          <Title text={title} />
          <Typography variant="h5Bold" component="h2" className="heading">
            <Text field={heading} />
          </Typography>
          <Typography variant="body1" component="p">
            <RichText innerHTML={description} />
          </Typography>
          {cta1 && cta1.value && cta1.value.text && (
            <Button
              hasBorder={false}
              field={cta1}
              className={classes.button}
              onClick={() => addAnalyticsInfo(get(cta1, 'value.text', ''))}
            />
          )}
          {cta2 && cta2.value && cta2.value.text && (
            <Box>
              <Button
                field={cta2}
                isStartIcon={true}
                hasBorder={false}
                className={classes.button}
                component="span"
                buttonType="secondary"
                size="small"
                onClick={() => addAnalyticsInfo(get(cta2, 'value.text', ''))}
              />
            </Box>
          )}
        </Grid>
        <Grid item xs={12} lg={6} className={classes.mediaArea}>
          {videoUrl.length === 0 ? (
            <Picture
              media={{
                image: mediaFields.image,
                mobileImage: mediaFields.mobileImage,
                aspectRatio: aspectRatios['3x2'],
              }}
              alternateAltText={heading && heading.value}
            />
          ) : isInsideChina(sitecoreContext) ? (
            <YoukuPlayer
              {...{
                media: {
                  image: mediaFields.image,
                  aspectRatio: aspectRatios['3x2'],
                },
                playerHeight: '100%',
                sitecoreContext,
                youkuVideo: mediaFields.youkuVideo,
                fallbackImage: mediaFields.fallbackImage,
                anayltyicsTitle,
                componentName,
                pageName,
                alternateAltText: title && title.value,
              }}
            />
          ) : (
                <NoSsr>
                  <CustomVideo
                    video={mediaFields.video}
                    posterImage={mediaFields.image}
                    mobileImage={mediaFields.mobileImage}
                    setPlayerDOMRef={setPlayerDOMRef}
                    aspectRatio={aspectRatios['3x2']}
                    playIcon={
                      mediaFields.playIcon &&
                      mediaFields.playIcon.value &&
                      mediaFields.playIcon.value.src
                    }
                    closeButtonSize="small"
                    alternateAltText={title && title.value}
                  />
                </NoSsr>
              )}
        </Grid>
      </Grid>
    </Grid>
  ) : null;
};

export default withErrorBoundary(
  withSitecoreContext()(withNamespaces()(Article))
);
