import makeStyles from '@material-ui/core/styles/makeStyles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  root: {
    ...theme.shape.componentSpacing,
    '& .heading': {
      marginBottom: pxToRem(16),
    },
    '& .MuiTypography-body1': {
      color: theme.palette.text.darkGrey,
    },
    '& .descriptionArea': {
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.paddingRight(pxToRem(144)),
        ...theme.mixins.paddingLeft(0),
      },
      '& .richText': {
        '& p:last-child': {
          marginBottom: 0,
        },
      },
    },

    '& .rowWrapper': {
      flexDirection: 'column-reverse',
      [theme.breakpoints.up('lg')]: {
        flexDirection: 'row',
      },

      '& .close-btn': {
        top: theme.spacing(-2),
        ...theme.mixins.right(theme.spacing(-2)),
        background: theme.palette.secondary.main,
        border: theme.borders.divider,
        [theme.breakpoints.up('lg')]: {
          top: theme.spacing(-3),
          ...theme.mixins.right(theme.spacing(-3)),
        },

        '&:hover': {
          background: theme.palette.primary.main,
          color: theme.palette.primary.contrastText,
        },
      },

      '&.rowReverse': {
        [theme.breakpoints.up('lg')]: {
          flexDirection: 'row-reverse',
        },
        '& .descriptionArea': {
          [theme.breakpoints.up('lg')]: {
            ...theme.mixins.paddingLeft(pxToRem(61)),
            ...theme.mixins.paddingRight(pxToRem(83)),
          },
        },
      },
    },
  },

  button: {
    marginTop: pxToRem(16),
  },

  mediaArea: {
    marginBottom: pxToRem(30),
    width: '100%',
    [theme.breakpoints.up('lg')]: {
      marginBottom: 0,
    },
    '& .plyr .plyr__video-wrapper': {
      paddingBottom: '66.67% !important',
      [theme.breakpoints.down('md')]: {
        width: '100vw',
      },
      '& > iframe': {
        height: '100%',
      },
    },
  },
}));

export default useStyles;
